.updraftcentral_dashboard {
	/** 
	* Basic styles applied to the WP-Optimize container
	*/
	div.updraftcentral_wpo_rows {
		font-size: 15px;
		margin-top: 20px;

		.wpo-not-found-message {
			margin-top: 20px;
			margin-bottom: 50px;
		}

		.wpo-not-installed,
		.wpo-not-activated {
			margin-bottom: 20px;
		}

		button#btn-install-activate,
		button#btn-activate {
			margin-bottom: 20px;
		}

		form#settings_form {
			border-bottom: 1px solid #9e9090;
			margin-bottom: 50px;
			padding-bottom: 60px;

		  	#retention-period {
			  	max-width: 100px;
			  	display: inline;
			  	margin: 0px 10px;
			}
		}

		.postbox {
			.inside {
				h3,
				h4 {
					padding-top: 0;
				}

				small {
				  	font-size: 15px;
				}

				label {
				  	font-weight: normal;
				}
			}
		}

		#wp_optimize_table_list_refresh {
		  	float: right;
		}

		tr.wp-optimize-settings {
		  	font-size: 13px;
		}

		input#wp-optimize {
		  	font-size: 18px !important;
		  	font-weight: normal;
		  	min-height: 34px;
		  	min-width: 200px;
		  	padding: 10px 20px !important;
		}

		div#wp-optimize-auto-options {
			> p {
			  	margin-bottom: 0;
			}

			> p:first-child {
			  	margin-bottom: 15px;
			}

			> p:last-child {
			  	margin-bottom: 20px;
			}
		}

		form#settings_form {
			small {
			  	font-size: 13px;
			  	font-style: italic;
			}

			input#wp-optimize-settings-save {
			  	font-size: 15px;
			  	font-weight: normal;
			}

			input.button {
			  	font-size: 15px;
			  	font-weight: normal;
			  	padding: 10px 20px !important;
			}
		}

		form#optimize_form {
			small {
			  	font-size: 13px;
			  	font-style: italic;
			}
		}

		button#wp-optimize-disable-enable-trackbacks-enable,
		button#wp-optimize-disable-enable-trackbacks-disable,
		button#wp-optimize-disable-enable-comments-enable,
		button#wp-optimize-disable-enable-comments-disable {
		  	font-size: 15px;
		  	font-weight: normal;
		  	padding: 10px 20px !important;
		}

		tr.wp-optimize-settings {
			label {
			  	font-weight: normal;
				margin-bottom: 0;
				margin-top: 0;
			}

			img.optimization_spinner {
				width: 25px;
			}
		}

		div#wp_optimize_status_box {
			border-top: 1px solid #9e9090;
			margin-top: 40px;
			padding-top: 20px;
		}

		.wp-optimize-settings-clean-transient {
			label {
			  	color: #9b0000;
			}
		}

		.wp-optimize-settings-clean-pingbacks {
			label {
			  	color: #9b0000;
			}
		}

		.wp-optimize-settings-clean-trackbacks {
			label {
			  	color: #9b0000;
			}
		}

		.wp-optimize-settings-clean-postmeta {
			label {
			  	color: #9b0000;
			}
		}

		.wp-optimize-settings-clean-orphandata {
			label {
			  	color: #9b0000;
			}
		}

		.wp-optimize-settings-clean-commentmeta {
			label {
			  	color: #9b0000;
			}
		}

		.display-none,
		.wpo_spinner {
		  	display: none;
		}

		#wp-optimize-auto-options {
		  	margin-left: 28px;
		  	opacity: 0.5;
		}

		table#wpoptimize_table_list {
		  	margin-bottom: 0;
		}

		span#wp_optimize_table_list_refresh {
		  	cursor: pointer;
		}

		p#additional_options_updraft_ring_logger {
			input {
			  	max-width: 100px;
			  	display: inline;
			  	margin: 0px 10px;
			}
		}

		.wp_uc_spinner {
			background: url('images/spinner-2x.gif');
			background-size: 20px 20px !important;
			width: 20px !important;
			height: 20px !important;
			position: absolute;
			margin-top: -10px;
		}

		table.widefat {
			border-spacing: 0;
			width: 100% !important;
			clear: both;
			margin: 0;

			tr.alternate {
				background-color: #f9f9f9;
			}
		}

		#wpo_sitelist_moreoptions {
			max-height: 150px;
			margin-right: 0;
		}

		#wpo_sitelist_moreoptions,
		#wpo_sitelist_moreoptions_cron {
			margin: 4px 16px 6px 0;
			margin-right: 16px;
			border: 1px dotted;
			padding: 6px 10px;
			max-height: 300px;
			overflow-y: scroll;
			overflow-x: hidden;
		}

		#wpo_settings_sites_list li,
		#wpo_settings_sites_list li a {
			font-size: 13px;
			line-height: 1.5;
		}

		#wpoptimize_table_list {
			font-size: 15px;
			margin-top: 25px;

			.tablesorter-filter-row {
				display: none !important;
			}

			.optimization_done_icon {
				color: #d94f00;
				font-size: 200%;
				display: inline-block;
				position: relative;
				top: 5px;
				left: -20px;
			}

			tr {
				td {
					vertical-align: middle;
				}
			}

			.optimization_spinner.wp_uc_spinner {
				margin-top: 10px;
				margin-left: 7px;
			}

			.optimization_done_icon {
				left: 0px;
			}
		}

		.visibility-hidden {
			visibility: hidden;
		}

		#wpoptimize_table_list_tables_not_found {
			display: none;
		}

		.run-single-table-optimization {
			line-height: 12px;
		}

		#wpoptimize_table_list_filter {
			width: 40%;
		}

		h3.updraftcentral-current-db-size {
			font-size: 20px;
		}

		.wpo_hidden {
			display: none;
		}

		img#wpo_import_spinner {
			margin-top: 12px;
			margin-left: 7px;
		}

		div.wpo_section {
			.wpo_col {
				.postbox {
					.inside {
						h5 {
							font-size: 16px;
							margin-top: -20px;
						}

						h3 {
							margin-top: 40px;
							font-size: 1.375rem;
						}
					}
				}
			}
		}

		div.wpo_section > form {
			margin-top: -20px;
		}

		#save_settings_reminder {
			display: none;
			color: #333;
			padding: 5px 10px;
			border: 1px solid #F00;
			background-color: #F0A5A4;
			border-radius: 5px;
			margin: 15px 0;
		}

		.select2-container--default.select2-container--focus {
			.select2-selection--multiple {
				border: solid black 1px;
				outline: 0;
			}
		}

		.select2-container--default {
			.select2-selection--multiple {
				background-color: white;
				border: 1px solid #AAA;
				border-radius: 4px;
				cursor: text;
			}
		}

		.select2-container {
			.select2-selection--multiple {
				box-sizing: border-box;
				cursor: pointer;
				display: block;
				min-height: 32px;
				user-select: none;
				-webkit-user-select: none;
			}
		}

		.wpo_remove_event {
			background-color: #F06666;
			color: #FFF;
			width: 20px;
			height: 20px;
			border-radius: 20px;
			display: inline-block;
			cursor: pointer;
			margin-top: 4px;
			margin-left: 4px;

			span.dashicons.dashicons-no-alt {
				margin-top: 4px;
			}
		}

		.wpo_schedule_fields {
			float: left;

			input.no_date_time_support {
				width: 90px;
			}

			select {
				max-width: 130px;
				padding: 0 0 0 5px !important;
				float: right;
				width: 110px !important;
				margin-left: 5px !important;
			}

			label {
				input {
					float: right;
					width: 110px !important;
					margin-left: 5px !important;
				}
			}

		}

		.wp-admin {
			select {
				padding: 2px;
				line-height: 28px;
				height: 28px;
				vertical-align: middle;
			}
		}

		.wpo_schedule_type {
			float: left;
			max-width: 130px;
			padding: 0 0 0 5px !important;
		}

		form#settings_form {
			#wp-optimize-auto-options {
				margin-left: 0px;
			}
		}

		#wpo_auto_events {
			margin-top: 15px;

			select,
			input {
				border-radius: 4px;
				line-height: 1.42;
				border: 1px solid #CCC;
				height: 27px;
				padding: 2px 6px;
				color: #555;
			}

			label {
				vertical-align: text-top;
				margin-left: 10px;
			}

			.wpo_optimizations {
				float: left;
				width: 38%;
				margin: 0;
				padding: 0;
			}

			.wpo_schedule {
				float: left;
				width: 12%;
			}

			.wpo_schedule_details {
				float: left;
				width: 25%;
			}

			.wpo_schedule_status {
				float: left;
				width: 11%;
			}

			.wpo_scheduled_event {
				.wpo_event_actions {
					float: left;
					width: 13%;

					span.wpo_remove_event {
						margin-top: -1px;

						.dashicons.dashicons-no-alt {
							margin-top: 1px;
						}
					}
				}
			}

			.clear {
				clear: both;
			}
		}

		.wpo_auto_event {
			input[type="date"],
			input[type="time"] {
				font-size: 13px;
			}

			input.select2-search__field {
				outline: none !important;
				border: 0px !important;
				font-size: 14px !important;
			}

			span.select2-selection--multiple {
				margin-bottom: 10px;
			}

			input,
			select,
			span,
			label {
				font-size: 14px !important;
			}

			.select2-container--default {
				width: 100% !important;
			}
		}

		.wpo_float_left {
			float: left;
		}

		.wpo_event_actions {
			width: 30px;
			float: right;
		}

		.wpo_event_status {
			width: 80px;
			float: left;

			label {
				input {
					float: left;
				}

				input[type="checkbox"] {
					margin-top: -1px !important;
				}
			}
		}

		.wpo_edit_event {
			.dashicons-edit {
				cursor: pointer !important;
			}
		}

		a#wpo-add-event:hover {
			cursor: pointer !important;
		}

		.wpo_auto_event_heading_container {
			font-weight: bold;
			border-top: 1px solid #333;
			border-bottom: 1px solid #333;
			padding: 5px 0;
			margin: 0;
		}

		.wpo_no_schedules {
			font-weight: bold;
			border-top: 1px solid #333;
			border-bottom: 1px solid #333;
			padding: 5px 0;
			margin: 0;
			margin-bottom: 0px;
		}

		.wpo_scheduled_event {
			border-bottom: 1px solid #A1A2A3;
			padding: 5px 0;
		}

		.wpo_schedule_details {
			span {
				margin-right: 5px;
				display: block;
			}
		}

		#wp-optimize-auto-options {
			color: #000;
		}

		.wpo_auto_event_heading_container,
		.wpo_logging_header {
			font-weight: bold;
			border-top: 1px solid #333;
			border-bottom: 1px solid #333;
			padding: 5px 0;
			margin: 0;
		}

		#wp-optimize-logging-options {
			.wpo_logging_logger_title,
			.wpo_logging_logger_row {
				float: left;
				width: 38%;
			}

			.wpo_logging_options_title,
			.wpo_logging_options_row {
				float: left;
				width: 37%;
			}

			.wpo_logging_status_title,
			.wpo_logging_status_row {
				float: left;
				width: 11%;
			}
		}

		a.wpo_delete_logger {
			background-color: #F06666;
			color: #FFF !important;
			width: 20px;
			height: 20px;
			border-radius: 20px;
			display: inline-block;
			cursor: pointer;
			margin-left: 4px !important;
			padding-top: 1px !important;
		}

		a.dashicons-edit {
			color: #6E7078 !important;
		}

		a.dashicons-edit:hover,
		a.wpo_delete_logger:hover,
		a.dashicons-edit:focus,
		a.wpo_delete_logger:focus {
			text-decoration: none !important;
			webkit-box-shadow: none !important;
			box-shadow: none !important;
		}

		.save_settings_reminder {
			display: none;
			color: #333;
			padding: 5px 10px;
			border: 1px solid #F00;
			background-color: #F0A5A4;
			border-radius: 5px;
			margin: 15px 0;
		}

		.wpo_logger_type {
			width: 90% !important;
			margin-top: 10px !important;
		}

		.wpo_add_logger_form > .wpo_delete_logger {
			margin-top: 18px !important;
			margin-left: 10px !important;
		}

		#wp-optimize-auto-options,
		#wp-optimize-logger-settings {
			opacity: 1 !important;
			color: #6E7078 !important;
		}

		.wpo_logging_actions_row,
		.wpo_event_actions,
		.wpo_logging_actions_title,
		.wpo_actions.wpo_auto_event_heading {
			text-align: right !important;
		}

		.wpo_logging_header {
			margin-bottom: 7px;
		}

		#wp-optimize-logger-settings {
			input,
			select,
			span,
			label,
			input.wpo_logger_addition_option {
				font-size: 14px !important; 
			}
		}

		.wpo_auto_event {
			.wpo_remove_event {
				.dashicons-no-alt {
					font-size: 20px !important;
					margin-top: 0px !important;
				}
			}
		}

		.wpo_alert_notice {
			background-color: #F06666;
			color: #FFF;
			padding: 5px;
			display: block;
			margin-bottom: 5px;
			border-radius: 5px;
		}

		.wpo_error_field {
			border-color: #F06666 !important;
		}

	}
}

span.select2-dropdown.select2-dropdown--below {
	margin-top: -10px !important;
}

span.select2-dropdown.select2-dropdown--above {
	margin-top: 0px !important;
}

#wp_optimize_status_box {
	padding: 7px 15px 10px 15px !important;
	border: 1px solid #CCC;
	margin-top: 0 !important;
	margin-bottom: 40px;
}

#wp_optimize_status_box {
	p {
		padding: 0 !important;
		margin: 0 !important;
	}
}

.wpo-run-optimizations__container {
	#wp-optimize {
		margin-bottom: 20px;
	}
}

#updraftcentral_tabs {
	margin-top: 80px;
	clear: both;
}

#updraftcentral_tab_headers {
	position: absolute;
	margin-top: -16px !important;
}

#updraftcentral_tab_headers li {
	float: left;
	font-size: 14px;
	line-height: 1.71428571;
	font-weight: 600;
	text-decoration: none;
	white-space: nowrap;
	margin-top: -15px;
}

#updraftcentral_tab_headers li > a {
	background: #FFF;
	box-shadow: 0 0 1px rgba(0,0,0,0.04);
	border-top-color: #0272AA;
	padding: 10px 20px;
	border-left: 1px solid #777;
	border-top: 1px solid #777;
	border-bottom: 1px solid #777 !important;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	color: #777 !important;
	font-weight: normal !important;
	border-right: 1px solid #777;
}

#updraftcentral_tab_headers li.ui-tabs-active {
	margin-top: -17px;
}

#updraftcentral_tab_headers li.ui-tabs-active + #updraftcentral_tab_headers li > a {
	border-left: none !important;
}

#updraftcentral_tab_headers li.ui-tabs-active > a {
	border-top: 3px solid #DE6726;
	display: inline-block;
	margin-top: -17px;
	border-bottom: 1px solid #FFF !important;
	border-right: 1px solid #777;
	font-size: 14px;
	color: #DE6726 !important;
	font-weight: 600 !important;
}

#updraftcentral_tab_contents {
	clear: both;
	border: 1px solid #777;
	margin-bottom: 30px;
	margin-top: 40px;
}

.wpo-take-a-backup label {
	font-size: 13px;
}

.wpo-table-list-filter {
	margin: 1em 0;
}

#updraftcentral_tabs_container label {
	font-size: 13px;
	line-height: 1.5;
	color: #01080F;
	opacity: 1;
	font-weight: 400;
	opacity: 0.7;
}

#updraftcentral_tabs_container p {
	font-size: 13px;
	line-height: 1.5;
}

#updraftcentral_tabs_container .button-link {
	margin: 0;
	margin-right: 0px;
	margin-left: 0px;
	padding: 0;
	padding-right: 0px;
	padding-left: 0px;
	box-shadow: none;
	border: 0;
	border-radius: 0;
	background: 0 0;
	cursor: pointer;
	text-align: left;
	color: #2271B1;
	text-decoration: underline;
	transition-property: border,background,color;
	transition-duration: .05s;
	transition-timing-function: ease-in-out;
}

#updraftcentral_tabs_container .button, .button-secondary {
	vertical-align: top;
}

#updraftcentral_tabs_container .button, .button-primary, .button-secondary {
	display: inline-block;
	text-decoration: none;
	font-size: 13px;
	line-height: 2.15384615;
	min-height: 30px;
	margin: 0;
	padding: 0 10px;
	cursor: pointer;
	-webkit-appearance: none;
	border-radius: 3px;
	white-space: nowrap;
	box-sizing: border-box;
}

#updraftcentral_tabs_container .button {
	width: auto;
	border: 0 none;
	font-size: 0.938rem;
	font-weight: 400;
	border-radius: 3px;
	padding: .4rem 1rem;
	line-height: 1.5;
	display: inline-block;
	border-radius: 3px;
	color: #0B0B0B !important;
	background-color: #DDD;
	margin: 0 5px;
}

#updraftcentral_tabs_container .button:hover {
	background-color: #C1BEBE;
}

#updraftcentral_tabs_container .button-primary {
	color: #FFF !important;
	background-color: #DE6726;
	margin: 0;
}

#updraftcentral_tabs_container .button-primary:hover {
	background-color: #B5521C;
}

#updraftcentral_tabs_container #optimize_current_db_size {
	font-size: 1.3rem !important;
}

#updraftcentral_tabs_container .button-link,
#updraftcentral_tabs_container .button-link:hover {
	background: none;
}

#updraftcentral_tabs_container input[type="number"] {
	width: 70px !important;
}

#updraftcentral_tabs_container input, select {
	margin: 0 5px;
}

#updraftcentral_tabs_container h3 {
	font-size: large !important;
}

#updraftcentral_tabs_container h4 {
	font-size: medium !important;
}

#updraftcentral_tabs_container .wpo-power-tweak .show-details,
#updraftcentral_tabs_container .wpo-power-tweak .show-faqs {
	font-size: 13px !important;
}

#updraftcentral_tabs_container .wpo-fieldgroup fieldset label {
	display: block;
	margin-bottom: 6px;
	line-height: 20px;
}

#updraftcentral_tabs_container {
	color: #3C434A;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 14px;
}

#updraftcentral_tabs_container #registered_image_sizes label {
	margin-bottom: 10px;
}

#updraftcentral_tabs_container #optimizations_list .wp-optimize-settings-optimization-info {
	opacity: 0.6;
}

#updraftcentral_tabs_container #wp-optimize-database-settings .wpo-fieldgroup small {
	margin-top: -10px;
	display: block;
}

#wp-optimize-wrap .updraftcentral_wpo_container {
	padding: 40px 10px;
}

#wp-optimize-wrap .updraftcentral_wpo_container button#btn-activate,
#wp-optimize-wrap .updraftcentral_wpo_container button#btn-install-activate {
	margin-top: 20px;
}

#wp-optimize-wrap #wp_optimize_preload_minify_status {
	vertical-align: middle;
	display: inline-block;
	line-height: 3;
}

#wpoptimize_table_list.tablesorter-default td,
#wpoptimize_table_list.tablesorter-default tr.odd > td,
#wpoptimize_table_list.tablesorter-default tr.odd > td {
	background-color: inherit !important;
}

#wpoptimize_table_list.tablesorter-default .tablesorter-header,
#wpoptimize_table_list.tablesorter-default .tablesorter-headerAsc,
#wpoptimize_table_list.tablesorter-default .tablesorter-headerDesc {
	background-image: none;
}

#wpoptimize_table_list.tablesorter-default td {
	font-size: 13px;
}

#updraftcentral_tabs_container .wpo_spinner {
	width: auto !important;
	top: 8px !important;
}

.wpo_shade {
	display: none !important;
}

a.wpo-info__trigger {
	padding: 10px !important;
	margin-left: -10px !important;
}

p.wpo-take-a-backup {
	display: inline-block;
	margin: 0;
	line-height: 1;
	padding-top: 8px;
}

#wp-optimize-wrap .switch .slider::before {
	top: 3px;
